<template>
	<div class="w-full h-full p-10">
		<div class="w-full h-full overflow-y-scroll flex flex-col">
			<p>距离第一次认识对方已经:</p>
			<br/>
			<p style="color: #D616C2;font-size: 60px;line-height: 60px;text-align: center;font-weight: 600;">{{time1}} <span style="font-size: 20px;">天</span></p>
			<br/>
			<p>距离第一次见面已经:</p>
			<br/>
			<p style="color: #F9BF53;font-size: 60px;line-height: 60px;text-align: center;font-weight: 600;">{{time2}} <span style="font-size: 20px;">天</span></p>

			<div class="flex-1 flex items-center justify-center">
				<img class="w-1/4" :src="$assets.Images.huanhuan" alt="">
				<!-- <p>&</p> -->
				<img class="w-1/3" :src="$assets.Images.lu" alt="">
			</div>
		</div>
	</div>
</template>

<script>
export default {
	components: {

	},
	watch: {

	},
	props: {

	},
	data() {
		return {
			time1: '',
			time2: '',
		}
	},
	mounted() {

	},
	created() {
		this.initTime();
	},
	beforeRouteLeave() {

	},
	methods: {
		initTime() {
			let asd1 = this.getTime("2022-12-06 00:00:00");
			this.time1 = asd1;
			let asd2 = this.getTime("2022-12-10 00:00:00");
			this.time2 = asd2;
		},
		getTime(time) {
			var new_date = new Date();
			var old_date = new Date(time);
			var difftime = (new_date - old_date)/1000;
			var days = parseInt(difftime/86400); 
			var hours = parseInt(difftime/3600)-24*days;     
			var minutes = parseInt(difftime%3600/60);
			var seconds = parseInt(difftime%60);
			// console.log("时间差是: "+days+"天, "+hours+"小时, "+minutes+"分钟, "+seconds+"秒");	
			return days
		},
	}
}
</script>

